	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>\n<p><em>必ずカテゴリごとに大コメントで区切る</em>。</p>\n<ul>\n<li>メンバ変数・グローバル変数には必ず1行コメントを入れる。</li>\n<li>関数定義にも必ず最低1行コメントを入れる。</li>\n<li>関数・メンバ変数の頭には<a href=\"http://ja.wikipedia.org/wiki/Javadoc\">javadoc</a>や<a href=\"http://ja.wikipedia.org/wiki/Doxygen\">doxygen</a>のような定型スタイルのコメントを入れた方が望ましい。(<a href=\"/tag/flan\">FLAN</a>ソース内のコメント参照)</li>\n<li>ローカル変数や式などの処理途中には分かりづらいときのみ1行コメントを入れる。(こちらは//でもかまわない)</li>\n<li><em>積極的にTODOやFIXMEを活用する</em>。</li>\n<li>TODOは実装したい機能があるが時間的や技術的にむりぽとかそういう時にその部分にコメントを設置し「TODO: (実装したいもの)」と記述する。</li>\n<li>FIXMEはバグを確認したがなんらかの理由(時間的とかめどいとか)で自力で修正不可能な場合その部分にコメントを設置し「FIXME: (こんなバグあるよ)」と記述する。</li>\n</ul>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2008-04-26T00:00:04+09:00","strDate":"2008/4/25","title":"コメント"}}},"pathContext":{"path":"/ja/blog/2008/04/26-コメント/","langKey":"ja"}}

